import React, { FC, ReactNode } from 'react';
import { NavLink, useLocation, useHistory } from 'react-router-dom';
import {
  Nav,
  NavList,
  NavItem,
  NavExpandable,
  Page,
  PageHeader,
  PageSidebar,
  SkipToContent,
  PageSection,
  PageSectionVariants,
  Avatar,
  Dropdown,
  DropdownToggle,
  PageHeaderTools,
  DropdownItem,
} from '@patternfly/react-core';
import { routes, IAppRoute, IAppRouteGroup } from '@app/routes';
import logo from '@app/bgimages/logo_phase_slash.svg';
import { useState } from 'react';
import { useKeycloak } from '@react-keycloak/web';
import { PowerOffIcon } from '@patternfly/react-icons';
import avatarImg from '@app/bgimages/img_avatar.svg';
import { deploymentModalKey } from '@app/pages/Deployments/Deployments';

interface IAppLayout {
  children: ReactNode;
}

const AppLayout: FC<IAppLayout> = ({ children }) => {
  const [isNavOpen, setIsNavOpen] = useState(true);
  const [isMobileView, setIsMobileView] = useState(true);
  const [isNavOpenMobile, setIsNavOpenMobile] = useState(false);
  const { keycloak } = useKeycloak();

  const onNavToggleMobile = () => {
    setIsNavOpenMobile(!isNavOpenMobile);
  };
  const onNavToggle = () => {
    setIsNavOpen(!isNavOpen);
  };
  const onPageResize = (props: { mobileView: boolean; windowSize: number }) => {
    setIsMobileView(props.mobileView);
  };

  function LogoImg() {
    const history = useHistory();
    function handleClick() {
      history.push('/');
    }
    return <img src={logo} onClick={handleClick} alt="PatternFly Logo" />;
  }

  const HeaderToolsDropdown = () => {
    const [isHeaderDropdownOpen, setIsHeaderDropdownOpen] = useState(false);

    return (
      <PageHeaderTools>
        <Dropdown
          isPlain
          position="right"
          id="user-dropdown"
          isOpen={isHeaderDropdownOpen}
          toggle={
            <DropdownToggle
              onToggle={setIsHeaderDropdownOpen}
              icon={<Avatar src={avatarImg} alt="Avatar image" className="pf-c-avatar pf-m-light" />}
            >
              {keycloak.tokenParsed?.preferred_username || keycloak.tokenParsed?.email || 'User'}
            </DropdownToggle>
          }
          dropdownItems={[
            <DropdownItem
              key="log out"
              id="log out"
              className="app-link"
              onClick={() => {
                localStorage.removeItem(deploymentModalKey);
                keycloak.logout();
              }}
              icon={<PowerOffIcon />}
            >
              Log out
            </DropdownItem>,
          ]}
        />
      </PageHeaderTools>
    );
  };

  const Header = (
    <PageHeader
      logo={<LogoImg />}
      showNavToggle
      isNavOpen={isNavOpen}
      onNavToggle={isMobileView ? onNavToggleMobile : onNavToggle}
      headerTools={<HeaderToolsDropdown />}
    />
  );

  const location = useLocation();

  const renderNavItem = (route: IAppRoute, index: number) => (
    <NavItem
      key={`${route.label}-${index}`}
      id={`${route.label}-${index}`}
      isActive={
        route.activeRelative ? location.pathname.includes(route.activeRelative) : route.path === location.pathname
      }
    >
      <NavLink exact={route.exact} to={route.path}>
        {route.label}
      </NavLink>
    </NavItem>
  );

  const renderNavGroup = (group: IAppRouteGroup, groupIndex: number) => (
    <NavExpandable
      key={`${group.label}-${groupIndex}`}
      id={`${group.label}-${groupIndex}`}
      title={group.label}
      isActive={group.routes.some((route) => route.path === location.pathname)}
    >
      {group.routes.map((route, idx) => route.label && renderNavItem(route, idx))}
    </NavExpandable>
  );

  const Navigation = (
    <Nav id="nav-primary-simple" theme="light">
      <NavList id="nav-list-simple">
        {routes.map(
          (route, idx) => route.label && (!route.routes ? renderNavItem(route, idx) : renderNavGroup(route, idx))
        )}
      </NavList>
    </Nav>
  );

  const Sidebar = <PageSidebar theme="light" nav={Navigation} isNavOpen={isMobileView ? isNavOpenMobile : isNavOpen} />;

  const pageId = 'primary-app-container';

  const PageSkipToContent = (
    <SkipToContent
      onClick={(event) => {
        event.preventDefault();
        const primaryContentContainer = document.getElementById(pageId);
        primaryContentContainer && primaryContentContainer.focus();
      }}
      href={`#${pageId}`}
    >
      Skip to Content
    </SkipToContent>
  );
  return (
    <Page
      mainContainerId={pageId}
      header={Header}
      sidebar={Sidebar}
      onPageResize={onPageResize}
      skipToContent={PageSkipToContent}
    >
      <PageSection variant={PageSectionVariants.light}>{children}</PageSection>
    </Page>
  );
};

export { AppLayout };
