import { generateBasePath } from '@app/routes';
import {
  Breadcrumb,
  BreadcrumbItem,
  Button,
  EmptyState,
  EmptyStateIcon,
  EmptyStateVariant,
  PageSection,
  Title,
} from '@patternfly/react-core';
import { DomainIcon } from '@patternfly/react-icons';
import React, { useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { useGetOrganizationById } from 'src/query/orgs';
import { AddOrganizationDomains, OrganizationDomainsTable } from './components';

const OrganizationDomains: React.FunctionComponent = () => {
  const { organization_id: orgId } = useParams();
  const [addDomain, setAddDomain] = useState(false);
  const { data: activeOrg, refetch } = useGetOrganizationById({
    pathParams: { realm: 'self', orgId },
  });

  return (
    <PageSection>
      <Breadcrumb>
        <BreadcrumbItem component="div">
          <Link to={generateBasePath('/organizations')}>Organizations</Link>
        </BreadcrumbItem>
        <BreadcrumbItem to="#" isActive>
          {activeOrg ? activeOrg.displayName : ''}
        </BreadcrumbItem>
        <BreadcrumbItem to="#" isActive>
          Domains
        </BreadcrumbItem>
      </Breadcrumb>
      <EmptyState variant={EmptyStateVariant.full}>
        <EmptyStateIcon icon={DomainIcon} />
        <Title headingLevel="h1" size="lg">
          {activeOrg ? activeOrg.displayName : 'Organization'} Domains
        </Title>
        <Button variant="primary" onClick={() => setAddDomain(true)}>
          Add Domain
        </Button>
        {addDomain && (
          <AddOrganizationDomains refresh={refetch} org={activeOrg} clearOrg={() => setAddDomain(false)} />
        )}
      </EmptyState>
      <OrganizationDomainsTable />
    </PageSection>
  );
};

export { OrganizationDomains};
