import * as React from 'react';
import { Redirect, Route, RouteComponentProps, Switch } from 'react-router-dom';
import { accessibleRouteChangeHandler } from '@app/utils/utils';
import { Deployments } from '@app/pages/Deployments/Deployments';
import { Organizations } from '@app/pages/Organizations/Organizations';
import { NotFound } from '@app/pages/NotFound/NotFound';
import { useDocumentTitle } from '@app/utils/useDocumentTitle';
import { LastLocationProvider, useLastLocation } from 'react-router-last-location';
import { OrganizationMembers } from '@app/pages/OrganizationMembers/OrganizationMembers';
import { OrganizationDomains } from '@app/pages/OrganizationDomains/OrganizationDomains';

let routeFocusTimer: number;
export interface IAppRoute {
  label?: string; // Excluding the label will exclude the route from the nav sidebar in AppLayout
  /* eslint-disable @typescript-eslint/no-explicit-any */
  component: React.ComponentType<RouteComponentProps<any>> | React.ComponentType<any>;
  /* eslint-enable @typescript-eslint/no-explicit-any */
  exact?: boolean;
  path: string;
  title: string;
  isAsync?: boolean;
  routes?: undefined;
  activeRelative?: string; // highlights the nav item for subitems
}

export interface IAppRouteGroup {
  label: string;
  routes: IAppRoute[];
}

export type AppRouteConfig = IAppRoute | IAppRouteGroup;

export const BASE_PATH = `${process.env.ROUTE_PATH || ''}`;

export const generateBasePath = (path: string) => `${BASE_PATH}${path}`;

const routes: AppRouteConfig[] = [
  {
    component: Deployments,
    exact: true,
    isAsync: true,
    label: 'Deployments',
    path: `${BASE_PATH}/deployments`,
    title: 'Deployments',
  },
  {
    component: Organizations,
    exact: true,
    isAsync: true,
    label: 'Organizations',
    path: `${BASE_PATH}/organizations`,
    title: 'Organizations',
    activeRelative: 'organizations',
  },
  {
    component: OrganizationMembers,
    exact: true,
    isAsync: true,
    // label: 'Organization Members',
    path: `${BASE_PATH}/organizations/:organization_id/members`,
    title: 'Organization Members',
  },
  {
    component: OrganizationDomains,
    exact: true,
    isAsync: true,
    // label: 'Organization Domains',
    path: `${BASE_PATH}/organizations/:organization_id/domains`,
    title: 'Organization Domains',
  },];

// a custom hook for sending focus to the primary content container
// after a view has loaded so that subsequent press of tab key
// sends focus directly to relevant content
const useA11yRouteChange = (isAsync: boolean) => {
  const lastNavigation = useLastLocation();
  React.useEffect(() => {
    if (!isAsync && lastNavigation !== null) {
      routeFocusTimer = accessibleRouteChangeHandler();
    }
    return () => {
      window.clearTimeout(routeFocusTimer);
    };
  }, [isAsync, lastNavigation]);
};

const RouteWithTitleUpdates = ({ component: Component, isAsync = false, title, ...rest }: IAppRoute) => {
  useA11yRouteChange(isAsync);
  useDocumentTitle(title);

  function routeWithTitle(routeProps: RouteComponentProps) {
    return <Component {...rest} {...routeProps} />;
  }

  return <Route render={routeWithTitle} {...rest} />;
};

const PageNotFound = ({ title }: { title: string }) => {
  useDocumentTitle(title);
  return <Route component={NotFound} />;
};

const flattenedRoutes: IAppRoute[] = routes.reduce(
  (flattened, route) => [...flattened, ...(route.routes ? route.routes : [route])],
  [] as IAppRoute[]
);

const AppRoutes = (): React.ReactElement => (
  <LastLocationProvider>
    <Switch>
      {flattenedRoutes.map(({ path, exact, component, title, isAsync }, idx) => (
        <RouteWithTitleUpdates
          path={path}
          exact={exact}
          component={component}
          key={idx}
          title={title}
          isAsync={isAsync}
        />
      ))}
      <Route path="/">
        <Redirect to={`${BASE_PATH}/deployments`} />
      </Route>
      <PageNotFound title="404 Page Not Found" />
    </Switch>
  </LastLocationProvider>
);

export { AppRoutes, routes };
