import React, { useEffect, useState } from 'react';
import {
  Button,
  Modal,
  ModalVariant,
  Form,
  FormGroup,
  Popover,
  TextInput,
  Checkbox,
  Grid,
} from '@patternfly/react-core';
import { HelpIcon, ExclamationCircleIcon } from '@patternfly/react-icons';
import { useKeycloak } from '@react-keycloak/web';
import { OrganizationRepresentation } from 'src/query/orgs';
import { useForm, Controller } from 'react-hook-form';
import toast from 'react-hot-toast';
import { API } from 'src/api';
import { forIn, omit } from 'lodash';
import { defaultRoles } from '@app/utils/checkUserRoles';

type Props = {
  refresh: () => void;
  closeModal: () => void;
  orgId: OrganizationRepresentation['id'];
};

const adminDRoles = defaultRoles.filter((r) => r.includes('manage') || r.includes('view'));
const developerDRoles = defaultRoles.filter((r) => r.includes('view'));

export const InviteOrganizationMembers: React.FC<Props> = ({ refresh, orgId, closeModal }) => {
  const { keycloak } = useKeycloak();
  const {
    handleSubmit,
    formState: { errors, isSubmitting },
    control,
    reset,
    setValue,
    watch,
  } = useForm({
    defaultValues: {
      email: '',
      'view-organization': false,
      'view-members': false,
      'view-roles': false,
      'view-invitations': false,
      'view-identity-providers': false,
      'manage-organization': false,
      'manage-members': false,
      'manage-roles': false,
      'manage-invitations': false,
      'manage-identity-providers': false,
    },
  });
  const [developerRoles, setDeveloperRoles] = useState(false);
  const [adminRoles, setAdminRoles] = useState(false);
  const watchAdminFields = watch(adminDRoles);
  const watchDeveloperFields = watch(developerDRoles);

  useEffect(() => {
    if (watchAdminFields.includes(false)) {
      if (adminRoles) setAdminRoles(false);
    } else {
      if (adminRoles === false) setAdminRoles(true);
    }
  }, [watchAdminFields]);

  useEffect(() => {
    if (watchDeveloperFields.includes(false)) {
      if (developerRoles) setDeveloperRoles(false);
    } else {
      if (developerRoles === false) setDeveloperRoles(true);
    }
  }, [watchDeveloperFields]);

  const checkAllRoles = (roleType: 'admin' | 'developer') => {
    if (roleType === 'admin') {
      adminDRoles.map((r) => setValue(r, !adminRoles));
      setAdminRoles(!adminRoles);
    }
    if (roleType === 'developer') {
      developerDRoles.map((r) => setValue(r, !developerRoles));
      setDeveloperRoles(!developerRoles);
    }
  };

  const submitForm = async (dep) => {
    try {
      const selectedRoles: string[] = [];
      forIn(omit(dep, 'email'), (value, key) => {
        if (value) selectedRoles.push(key);
      });
      const inviteMemberResp = await API.Orgs.inviteUserToOrg({
        orgId,
        inviteDetails: {
          email: dep.email,
          inviterId: keycloak.tokenParsed?.sub,
          roles: selectedRoles,
        },
      });

      if (inviteMemberResp.ok) {
        toast.success(`Member has been sent an invitation.`);
        refresh();
        closeModal();
        return;
      }
      const errorResp = await inviteMemberResp.json();
      toast.error(`Member was not able to be invited. Please try again. (${errorResp.error})`);
    } catch (e) {
      console.log('[Error] Failed to update the organization.');
    }
  };

  const hasError = (key: string) => (errors[key] ? 'error' : 'default');

  return (
    <>
      <Modal
        variant={ModalVariant.small}
        title="Invite New Member"
        description="Enter new member email below to invite to organization."
        isOpen={true}
        onClose={closeModal}
        actions={[
          <Button key="create" variant="primary" form="new-member-invite" type="submit">
            {isSubmitting ? 'Sending invite...' : 'Invite'}
          </Button>,
          <Button key="cancel" variant="link" onClick={closeModal}>
            Cancel
          </Button>,
        ]}
      >
        <Form id="new-member-invite" onSubmit={handleSubmit(submitForm)}>
          <FormGroup
            label="Email"
            labelIcon={
              <Popover headerContent={<div>Email</div>} bodyContent={<div>The email of the new member to invite.</div>}>
                <button
                  type="button"
                  aria-label="More info for email field."
                  onClick={(e) => e.preventDefault()}
                  aria-describedby="modal-with-form-form-email"
                  className="pf-c-form__group-label-help"
                >
                  <HelpIcon noVerticalAlign />
                </button>
              </Popover>
            }
            isRequired
            fieldId="modal-with-form-form-email"
            validated={hasError('email')}
            helperTextInvalid={'Email is a required field. '}
            helperTextInvalidIcon={<ExclamationCircleIcon />}
          >
            <Controller
              name="email"
              control={control}
              rules={{ required: true, min: 3 }}
              render={({ field }) => (
                <TextInput
                  id="modal-with-form-form-email"
                  isRequired
                  type="text"
                  validated={hasError('email')}
                  {...field}
                />
              )}
            />
          </FormGroup>
          <FormGroup
            label="Roles"
            labelIcon={
              <Popover
                headerContent={<div>Roles</div>}
                bodyContent={<div>Roles to assign the user. Admin gets all roles. Developer gets view only.</div>}
              >
                <button
                  type="button"
                  aria-label="More info for roles field."
                  onClick={(e) => e.preventDefault()}
                  aria-describedby="modal-with-roles"
                  className="pf-c-form__group-label-help"
                >
                  <HelpIcon noVerticalAlign />
                </button>
              </Popover>
            }
            fieldId="modal-with-roles"
          >
            <Grid hasGutter sm={6}>
              <Checkbox
                id="developer-roles"
                label="Developer"
                isChecked={developerRoles}
                onChange={() => checkAllRoles('developer')}
              />
              <Checkbox id="admin-roles" label="Admin" isChecked={adminRoles} onChange={() => checkAllRoles('admin')} />
            </Grid>
            <Grid hasGutter sm={6} style={{ marginTop: '1.5rem' }}>
              {defaultRoles.map((role) => (
                <Controller
                  name={role}
                  key={role}
                  control={control}
                  // rules={{ required: true }}
                  render={({ field, field: { value } }) => (
                    <Checkbox id={role} label={role} isChecked={value} {...field} />
                  )}
                />
              ))}
            </Grid>
          </FormGroup>
        </Form>
      </Modal>
    </>
  );
};
