import { ConfirmationDialog } from '@app/components/ConfirmationDialog/confirmation-dialog';
import {
  Bullseye,
  Button,
  Chip,
  ChipGroup,
  EmptyState,
  EmptyStateBody,
  EmptyStateIcon,
  EmptyStateVariant,
  OverflowMenu,
  OverflowMenuContent,
  OverflowMenuGroup,
  OverflowMenuItem,
  Spinner,
  Title,
} from '@patternfly/react-core';
import { ExclamationCircleIcon, SearchIcon } from '@patternfly/react-icons';
import { TableComposable, Tbody, Td, Th, Thead, Tr } from '@patternfly/react-table';
import { useKeycloak } from '@react-keycloak/web';
import { KeycloakProfile } from 'keycloak-js';
import React, { useEffect, useState } from 'react';
import toast from 'react-hot-toast';
import { useParams } from 'react-router-dom';
import { baseUrl } from 'src';
import { API } from 'src/api';
import { OrganizationRepresentation, OrganizationDomainRepresentation, useGetOrganizationMemberships, UserRepresentation, useGetOrganizationDomains } from 'src/query/orgs';
import { AddOrganizationDomains } from './add-organization-domains';
import { checkUserRoleForOrg } from '@app/utils/checkUserRoles';

type RoleCheck = {
  memberId: UserRepresentation['id'];
  roles: string[];
};

const columnNames = {
  domain: 'Domain',
  verified: 'Verified',
};

export const OrganizationDomainsTable: React.FC = () => {
  const { keycloak } = useKeycloak();
  const { organization_id: orgId } = useParams();
  const {
    data: domains = [],
    error,
    isLoading,
    refetch,
  } = useGetOrganizationDomains({
    pathParams: { realm: 'self', orgId },
  });
  const [user, setUser] = useState<KeycloakProfile>();
  const [removeDomain, setRemoveDomain] = useState<OrganizationDomainRepresentation | null>(null);
  const [editOrganization, setEditOrganization] = useState<OrganizationRepresentation | null>(null);
  const [isRemovingDomain, setIsRemovingDomain] = useState(false);

  const loadUser = async () => {
    setUser(await keycloak.loadUserProfile());
  };

  const removeTheDomain = async () => {
    setIsRemovingDomain(true);

    if (!editOrganization.domains.includes(removeDomain?.domain_name)) {
      toast.error(
        `${removeDomain.domain_name} is not part of organization ${removeDomain.displayName}.`
      );
    } else {
      editOrganization.domains = editOrganization.domains.filter(d => d != removeDomain?.domain_name);
      try {
        const updateOrgResp = await API.Orgs.editOrg({
          org: {
            ...editOrganization
          },
        });

        if (updateOrgResp.ok) {
          toast.success(`${removeDomain?.domain_name} has been added to organization ${editOrganization.displayName}`);
          refetch();
          setRemoveDomain(null);
        }
        const errorResp = await updateOrgResp.json();
        toast.error(
          `${removeDomain?.domain_name} was not able to be added to organization ${editOrganization.displayName}. Please try again. (${errorResp.error})`
        );
      } catch (e) {
        console.log('[Error] Failed to update the organization.');
        return false;
      }
      setIsRemovingDomain(false);
      return false;
    }
  };

  useEffect(() => {
    loadUser();
  }, []);

  return (
    <>
      {removeDomain && (
        <ConfirmationDialog
          isOpen={!!removeDomain}
          title={`Remove domain`}
          body={`Remove the domain ${removeDomain?.domain_name} from the organization.`}
          confirmText="Remove"
          confirmVariant="danger"
          onModalCancel={() => setRemoveDomain(null)}
          onModalConfirm={() => removeTheDomain()}
          disableConfirm={isRemovingDomain}
        />
      )}
      <TableComposable aria-label="Actions table">
        <Thead>
          <Tr>
            <Th>{columnNames.domain}</Th>
            <Th>{columnNames.verified}</Th>
            <Td></Td>
          </Tr>
        </Thead>
        <Tbody>
          {isLoading && (
            <Tr>
              <Td colSpan={5}>
                <Bullseye>
                  <EmptyState>
                    <EmptyStateIcon variant="container" component={Spinner} />
                    <Title size="lg" headingLevel="h2">
                      Loading
                    </Title>
                  </EmptyState>
                </Bullseye>
              </Td>
            </Tr>
          )}
          {error && !isLoading && (
            <Tr>
              <Td colSpan={5}>
                <EmptyState variant={EmptyStateVariant.small}>
                  <EmptyStateIcon icon={ExclamationCircleIcon} />
                  <Title headingLevel="h2" size="lg">
                    No organization domains found
                  </Title>
                  <EmptyStateBody>
                    There was an error retrieving data. Check your connection and reload the page.
                  </EmptyStateBody>
                </EmptyState>
              </Td>
            </Tr>
          )}
          {domains.length === 0 && !isLoading && !error && (
            <Tr>
              <Td colSpan={5}>
                <EmptyState>
                  <EmptyStateIcon icon={SearchIcon} />
                  <Title headingLevel="h2" size="lg">
                    No organization domains found
                  </Title>
                  <EmptyStateBody>No organization domains are available.</EmptyStateBody>
                </EmptyState>
              </Td>
            </Tr>
          )}
          {domains.length > 0 &&
            !isLoading &&
            !error &&
            domains.map((domain) => {
              return (
                <Tr key={domain.domain_name}>
                  <Td dataLabel={columnNames.domain}>{domain.domain_name}</Td>
                  <Td dataLabel={columnNames.verified}>{domain.verified}</Td>
                  <Td isActionCell>
                    <OverflowMenu breakpoint="md">
                      <OverflowMenuContent>
                        <OverflowMenuGroup groupType="button">
                          <OverflowMenuItem>
                            <Button
                              variant="link"
                              isDanger
                              onClick={() => setRemoveDomain(domain)}
                              isDisabled={!checkUserRoleForOrg('manage-organization', orgId)}
                            >
                              Remove
                            </Button>
                          </OverflowMenuItem>
                        </OverflowMenuGroup>
                      </OverflowMenuContent>
                    </OverflowMenu>
                  </Td>
                </Tr>
              );
            })}
        </Tbody>
      </TableComposable>
      {editOrganization && (
        <AddOrganizationDomains
          org={editOrganization}
          refresh={() => refetch()}
          clearOrg={() => setEditOrganization(null)}
        />
      )}
    </>
  );
};
