import React, { useState } from 'react';
import { TableComposable, Thead, Tr, Th, Tbody, Td } from '@patternfly/react-table';
import {
  Button,
  Dropdown,
  KebabToggle,
  OverflowMenu,
  OverflowMenuContent,
  OverflowMenuGroup,
  OverflowMenuItem,
  Title,
  EmptyState,
  EmptyStateVariant,
  EmptyStateIcon,
  EmptyStateBody,
  Bullseye,
  Spinner,
} from '@patternfly/react-core';
import {
  ExclamationCircleIcon,
  EditAltIcon,
  SearchIcon,
  UsersAltIcon,
  ExternalLinkSquareAltIcon,
} from '@patternfly/react-icons';
import { useKeycloak } from '@react-keycloak/web';
import { OrganizationRepresentation, useGetOrganizations } from 'src/query/orgs';
import { EditOrganization } from './edit-organization';
import { Link } from 'react-router-dom';
import { generateBasePath } from '@app/routes';
import { checkUserRoleForOrg, checkUserAllDefaultRolesForOrg } from '@app/utils/checkUserRoles';
import { render } from 'enzyme';

const columnNames = {
  name: 'Name',
  id: 'ID',
  displayName: 'Display Name',
};

export const OrganizationsTable: React.FC = () => {
  const { keycloak } = useKeycloak();
  const {
    data: organizations = [],
    error,
    isLoading,
    refetch,
  } = useGetOrganizations({
    pathParams: { realm: 'self' },
    headers: {
      Authorization: `Bearer ${keycloak.token}`,
    },
  });
  const [editOrganization, setEditOrganization] = useState<OrganizationRepresentation | null>(null);

  const getPortalLink = (orgId) => {
    const link = `${keycloak.authServerUrl}realms/${keycloak.realm}/wizard/?org_id=${encodeURIComponent(orgId)}`;
    window.open(link);
  };

  return (
    <>
      <TableComposable aria-label="Actions table">
        <Thead>
          <Tr>
            <Th>{columnNames.id}</Th>
            <Th>{columnNames.name}</Th>
            <Th>{columnNames.displayName}</Th>
            <Td></Td>
          </Tr>
        </Thead>
        <Tbody>
          {isLoading && (
            <Tr>
              <Td colSpan={5}>
                <Bullseye>
                  <EmptyState>
                    <EmptyStateIcon variant="container" component={Spinner} />
                    <Title size="lg" headingLevel="h2">
                      Loading
                    </Title>
                  </EmptyState>
                </Bullseye>
              </Td>
            </Tr>
          )}
          {error && !isLoading && (
            <Tr>
              <Td colSpan={5}>
                <EmptyState variant={EmptyStateVariant.small}>
                  <EmptyStateIcon icon={ExclamationCircleIcon} />
                  <Title headingLevel="h2" size="lg">
                    No Organizations found
                  </Title>
                  <EmptyStateBody>
                    There was an error retrieving data. Check your connection and reload the page.
                  </EmptyStateBody>
                </EmptyState>
              </Td>
            </Tr>
          )}
          {organizations.length === 0 && !isLoading && !error && (
            <Tr>
              <Td colSpan={5}>
                <EmptyState>
                  <EmptyStateIcon icon={SearchIcon} />
                  <Title headingLevel="h2" size="lg">
                    No Organizations found
                  </Title>
                  <EmptyStateBody>No Organizations are available.</EmptyStateBody>
                </EmptyState>
              </Td>
            </Tr>
          )}
          {organizations.length > 0 &&
            !isLoading &&
            !error &&
            organizations.map((org) => {
              const canSsoSetup = checkUserAllDefaultRolesForOrg(org.id);
              const MembersButton = () => (
                <Button
                  variant="link"
                  icon={<UsersAltIcon />}
                  isDisabled={!checkUserRoleForOrg('view-members', org.id)}
                >
                  Members
                </Button>
              );
              const DomainsButton = () => (
                <Button
                  variant="link"
                  icon={<UsersAltIcon />}
                  isDisabled={!checkUserRoleForOrg('view-organization', org.id)}
                >
                  Domains
                </Button>
              );
              return (
                <Tr key={org.name}>
                  <Td dataLabel={columnNames.id}>{org.id}</Td>
                  <Td dataLabel={columnNames.name}>{org.name}</Td>
                  <Td dataLabel={columnNames.displayName}>{org.displayName}</Td>
                  <Td isActionCell>
                    <OverflowMenu breakpoint="md">
                      <OverflowMenuContent>
                        <OverflowMenuGroup groupType="button">
                          <OverflowMenuItem>
                            <Button
                              variant="link"
                              icon={<EditAltIcon />}
                              onClick={() => setEditOrganization(org)}
                              isDisabled={!checkUserRoleForOrg('manage-organization', org.id)}
                            >
                              Edit
                            </Button>
                          </OverflowMenuItem>
                          <OverflowMenuItem>
                            {checkUserRoleForOrg('view-members', org.id) ? (
                              <Link to={generateBasePath(`/organizations/${org.id}/members`)}>
                                <MembersButton />
                              </Link>
                            ) : (
                              <MembersButton />
                            )}
                          </OverflowMenuItem>
                          {/*
                          <OverflowMenuItem>
                            {checkUserRoleForOrg('view-organization', org.id) ? (
                              <Link to={generateBasePath(`/organizations/${org.id}/domains`)}>
                                <DomainsButton />
                              </Link>
                            ) : (
                              <DomainsButton />
                            )}
                          </OverflowMenuItem>
                          <OverflowMenuItem>
                            <Button
                              variant="link"
                              isDisabled={!canSsoSetup}
                              icon={<ExternalLinkSquareAltIcon />}
                              onClick={() => getPortalLink(org.id)}
                            >
                              SSO Setup
                            </Button>
                          </OverflowMenuItem>
                          */} 
                        </OverflowMenuGroup>
                      </OverflowMenuContent>
                    </OverflowMenu>
                  </Td>
                </Tr>
              );
            })}
        </Tbody>
      </TableComposable>
      {editOrganization && (
        <EditOrganization org={editOrganization} refresh={() => refetch()} clearOrg={() => setEditOrganization(null)} />
      )}
    </>
  );
};
