import { ConfirmationDialog } from '@app/components/ConfirmationDialog/confirmation-dialog';
import {
  Bullseye,
  Button,
  Chip,
  ChipGroup,
  EmptyState,
  EmptyStateBody,
  EmptyStateIcon,
  EmptyStateVariant,
  OverflowMenu,
  OverflowMenuContent,
  OverflowMenuGroup,
  OverflowMenuItem,
  Spinner,
  Title,
} from '@patternfly/react-core';
import { ExclamationCircleIcon, SearchIcon } from '@patternfly/react-icons';
import { TableComposable, Tbody, Td, Th, Thead, Tr } from '@patternfly/react-table';
import { useKeycloak } from '@react-keycloak/web';
import { KeycloakProfile } from 'keycloak-js';
import React, { useEffect, useState } from 'react';
import toast from 'react-hot-toast';
import { useParams } from 'react-router-dom';
import { baseUrl } from 'src';
import { API } from 'src/api';
import { OrganizationRepresentation, useGetOrganizationMemberships, UserRepresentation } from 'src/query/orgs';
import { InviteOrganizationMembers } from './invite-organization-members';

type RoleCheck = {
  memberId: UserRepresentation['id'];
  roles: string[];
};

const columnNames = {
  name: 'Name',
  email: 'Email',
  roles: 'Roles',
};

export const OrganizationMembersTable: React.FC = () => {
  const { keycloak } = useKeycloak();
  const { organization_id: orgId } = useParams();
  const {
    data: members = [],
    error,
    isLoading,
    refetch,
  } = useGetOrganizationMemberships({
    pathParams: { realm: 'self', orgId },
  });
  const [user, setUser] = useState<KeycloakProfile>();
  const [hasManageMembersRole, setHasManageMembersRole] = useState(false);
  const [removeMember, setRemoveMember] = useState<UserRepresentation | null>(null);
  const [isRemovingMember, setIsRemovingMember] = useState(false);

  const [editOrganization, setEditOrganization] = useState<OrganizationRepresentation | null>(null);
  const [roleChecks, setRoleChecks] = useState<RoleCheck[]>([]);

  const loadUser = async () => {
    setUser(await keycloak.loadUserProfile());
  };

  useEffect(() => {
    loadUser();
  }, []);

  const fetchRoleCheck = async ({ userId }) => {
    try {
      const roleCheck = await API.Orgs.getRolesForMemberInOrg({ userId, orgId });

      if (roleCheck.ok) {
        return await roleCheck.json();
      }
    } catch (e) {
      console.log('[Error] Role check for user failed.');
      return false;
    }
    return false;
  };

  const checkUserRole = async ({ userId, roleName }) => {
    try {
      const roleCheck = await API.Orgs.checkMemberRoleInOrg({ roleName, orgId, userId });

      if (roleCheck.ok) {
        return true;
      }
    } catch (e) {
      console.log('[Error] Role check for user failed.');
      return false;
    }
    return false;
  };

  const checkForManageMembersRole = async () => {
    const findUser = roleChecks.find((rc) => rc.memberId === user?.id);
    if (findUser?.roles.includes('manage-members')) {
      setHasManageMembersRole(true);
    } else {
      const getRole = await checkUserRole({
        userId: user?.id || (await keycloak.loadUserProfile()).id,
        roleName: 'manage-members',
      });
      setHasManageMembersRole(getRole);
    }
  };

  const getRoles = async () => {
    const rolesPromise: RoleCheck[] = await Promise.all(
      members.map(async (member) => ({
        memberId: member.id,
        roles: await fetchRoleCheck({ userId: member.id }),
      }))
    );
    setRoleChecks(rolesPromise);

    checkForManageMembersRole();
  };

  const removeTheMember = async () => {
    setIsRemovingMember(true);
    try {
      const removeMemberResp = await fetch(`${baseUrl}/${keycloak.realm}/orgs/${orgId}/members/${removeMember?.id}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${keycloak.token}`,
        },
      });

      if (removeMemberResp.ok) {
        toast.success(`${removeMember.username} has been removed from organization.`);
        refetch();
        setRemoveMember(null);
      }
    } catch (e) {
      console.log('[Error] Role check for user failed.');
      return false;
    }
    setIsRemovingMember(false);
    return false;
  };

  useEffect(() => {
    if (members.length > 0) getRoles();
  }, [members]);

  return (
    <>
      {removeMember && (
        <ConfirmationDialog
          isOpen={!!removeMember}
          title={`Remove member`}
          body={`Remove this member ${removeMember.username} from the organization.`}
          confirmText="Remove"
          confirmVariant="danger"
          onModalCancel={() => setRemoveMember(null)}
          onModalConfirm={() => removeTheMember()}
          disableConfirm={isRemovingMember}
        />
      )}
      <TableComposable aria-label="Actions table">
        <Thead>
          <Tr>
            <Th>{columnNames.name}</Th>
            <Th>{columnNames.email}</Th>
            <Th>{columnNames.roles}</Th>
            <Td></Td>
          </Tr>
        </Thead>
        <Tbody>
          {isLoading && (
            <Tr>
              <Td colSpan={5}>
                <Bullseye>
                  <EmptyState>
                    <EmptyStateIcon variant="container" component={Spinner} />
                    <Title size="lg" headingLevel="h2">
                      Loading
                    </Title>
                  </EmptyState>
                </Bullseye>
              </Td>
            </Tr>
          )}
          {error && !isLoading && (
            <Tr>
              <Td colSpan={5}>
                <EmptyState variant={EmptyStateVariant.small}>
                  <EmptyStateIcon icon={ExclamationCircleIcon} />
                  <Title headingLevel="h2" size="lg">
                    No organization members found
                  </Title>
                  <EmptyStateBody>
                    There was an error retrieving data. Check your connection and reload the page.
                  </EmptyStateBody>
                </EmptyState>
              </Td>
            </Tr>
          )}
          {members.length === 0 && !isLoading && !error && (
            <Tr>
              <Td colSpan={5}>
                <EmptyState>
                  <EmptyStateIcon icon={SearchIcon} />
                  <Title headingLevel="h2" size="lg">
                    No organization members found
                  </Title>
                  <EmptyStateBody>No organization members are available.</EmptyStateBody>
                </EmptyState>
              </Td>
            </Tr>
          )}
          {members.length > 0 &&
            !isLoading &&
            !error &&
            members.map((member) => {
              if (member.username === `org-admin-${orgId}`) {
                return <Tr key={member.id}></Tr>;
              }
              const rc = roleChecks.find((rc) => rc.memberId === member.id);
              return (
                <Tr key={member.id}>
                  <Td dataLabel={columnNames.name}>{member.username}</Td>
                  <Td dataLabel={columnNames.email}>{member.email}</Td>
                  <Td dataLabel={columnNames.roles}>
                    <ChipGroup numChips={10}>
                      {rc?.roles
                        .sort((a, b) => a.length - b.length)
                        .map((r) => (
                          <Chip key={r.id} isReadOnly title="Detail">
                            {r.name}
                          </Chip>
                        ))}
                    </ChipGroup>
                  </Td>
                  <Td isActionCell>
                    <OverflowMenu breakpoint="md">
                      <OverflowMenuContent>
                        <OverflowMenuGroup groupType="button">
                          <OverflowMenuItem>
                            <Button
                              variant="link"
                              isDanger
                              onClick={() => setRemoveMember(member)}
                              isDisabled={member.id === user?.id || !hasManageMembersRole}
                            >
                              Remove
                            </Button>
                          </OverflowMenuItem>
                        </OverflowMenuGroup>
                      </OverflowMenuContent>
                    </OverflowMenu>
                  </Td>
                </Tr>
              );
            })}
        </Tbody>
      </TableComposable>
      {editOrganization && (
        <InviteOrganizationMembers
          org={editOrganization}
          refresh={() => refetch()}
          clearOrg={() => setEditOrganization(null)}
        />
      )}
    </>
  );
};
