import React, { ReactElement, useEffect, useState } from 'react';
import {
  Button,
  Modal,
  ModalVariant,
  Form,
  FormGroup,
  Popover,
  TextInput,
  FormSelect,
  FormSelectOption,
  Flex,
  FlexItem,
  Card,
  CardBody,
  Bullseye,
  List,
  ListItem,
  Title,
  Text,
  TextVariants,
  Spinner,
  ValidatedOptions,
} from '@patternfly/react-core';
import { HelpIcon, ExclamationCircleIcon, CheckCircleIcon, CheckIcon } from '@patternfly/react-icons';
import { OrganizationRepresentation } from 'src/query/orgs';
import { useForm, Controller } from 'react-hook-form';
import toast from 'react-hot-toast';
import { nanoid } from 'nanoid';
import { debounce } from 'lodash';
import { API } from 'src/api';
import cs from 'classnames';
import { checkUserRoleForOrg } from '@app/utils/checkUserRoles';

type Props = {
  refresh: (val: string) => void;
  isModalOpen: boolean;
  setModalOpen: (open: boolean) => void;
};

const regions = [
  {
    id: 'us-west-2',
    displayName: 'us-west-2',
  },
  {
    id: 'eu-central-1',
    displayName: 'eu-central-1',
  },
];

type SelectionProps = {
  id: 'shared' | 'dedicated';
  title: string;
  subtitle: string;
  listItems: ReactElement[] | string[];
};

export const CreateDeployment: React.FC<Props> = ({ refresh, isModalOpen, setModalOpen }) => {
  const {
    handleSubmit,
    formState: { errors, isSubmitting },
    control,
    reset,
  } = useForm();

  const [orgs, setOrgs] = useState<OrganizationRepresentation[]>([]);
  const [selectedDeploymentOption, setSelectedDeploymentOption] = useState<SelectionProps['id']>('shared');
  const [nameValid, setNameValid] = useState<true | false | null | 'checking'>(null);

  const getUserOrgs = async () => setOrgs(await API.User.getUserOrgs());

  useEffect(() => {
    if (isModalOpen) {
      getUserOrgs();
    }

    if (isModalOpen === false) {
      reset();
    }
  }, [isModalOpen]);

  const handleModalToggle = () => {
    setModalOpen(!isModalOpen);
  };

  const submitForm = async (dep) => {
    try {
      toast('Creating deployment. Please standby as this can take a few moments...');
      const createDeployment = await API.Orgs.createDeployment({
        name: dep.name,
        organization_id: dep.organization,
        region: dep.region,
      });

      if (createDeployment.ok) {
        toast.success(`${dep.name} deployment created successfully.`);
        setModalOpen(false);
        refresh(dep.name + nanoid());
        reset();
        return;
      }
      const errorResp = await createDeployment.json();
      toast.error(`${dep.name} deployment was not able to be created. (${errorResp.error})`);
    } catch (e) {
      console.log('[Error] Not able to create the deployment.');
    }
  };

  const checkAvailabilty = debounce(
    async (e: React.KeyboardEventHandler<HTMLInputElement>) => {
      // if more than 2 characters then make API call to check for unique name
      if (e.target.value?.length > 2) {
        setNameValid('checking');

        try {
          const checkValue = await API.Orgs.isDeploymentNameUnique({ deploymentName: e.target.value });
          return setNameValid(checkValue.available);
        } catch (e) {
          console.log(e);
        }
      }
      return setNameValid(null);
    },
    300,
    {
      maxWait: 1000,
    }
  );

  // Filter only orgs that user has the right role for
  const manageRoleOrgs = orgs.filter((org) => checkUserRoleForOrg('manage-organization', org.id));

  const SelectionOption = ({ id, title, subtitle, listItems }: SelectionProps) => (
    <FlexItem flex={{ default: 'flex_1' }} alignSelf={{ default: 'alignSelfStretch' }}>
      <Card
        isFullHeight
        isRounded
        style={{
          border: `3px solid ${
            selectedDeploymentOption === id ? 'var(--pf-global--palette--blue-400)' : 'var(--pf-global--palette--white)'
          }`,
          boxShadow: 'var(--pf-global--BoxShadow--md)',
        }}
        className={cs('create-deployment-card', {
          disabled: id === 'dedicated',
        })}
        onClick={() => {
          if (id === 'dedicated') return;
          setSelectedDeploymentOption(id);
        }}
      >
        <CardBody style={{ position: 'relative' }}>
          {selectedDeploymentOption === id && (
            <div style={{ position: 'absolute', right: 7, top: 5 }}>
              <CheckIcon color="var(--pf-global--palette--blue-400)" style={{ width: '12px' }} />
            </div>
          )}
          <Bullseye style={{ flexDirection: 'column', justifyContent: 'flex-start' }}>
            <Title headingLevel="h1" style={{ marginBottom: '1rem' }}>
              {title}
            </Title>
            <Text component={TextVariants.h3} style={{ maxWidth: '70%', marginBottom: '1rem', textAlign: 'center' }}>
              {subtitle}
            </Text>
            <List isPlain iconSize="large">
              {listItems.map((li, i) => (
                <ListItem
                  icon={<CheckCircleIcon color="var(--pf-global--palette--blue-400)" style={{ width: '18px' }} />}
                  key={i}
                >
                  {li}
                </ListItem>
              ))}
            </List>
          </Bullseye>
        </CardBody>
      </Card>
    </FlexItem>
  );

  let isNameValid = ValidatedOptions.default;
  let nameHelperText = 'Name must be unique.';
  if (nameValid === false) {
    isNameValid = ValidatedOptions.error;
    nameHelperText = 'Deployment name is not available for use. Please try another.';
  } else if (nameValid) {
    isNameValid = ValidatedOptions.success;
    nameHelperText = 'Deployment name is available for use.';
  } else if (errors['name']) {
    nameHelperText = 'Deployment name is a required field. Must be at least 3 characters.';
  }
  if (nameValid === 'checking') {
    nameHelperText = 'Checking for deployment name availability...';
  }

  return (
    <>
      <Button variant="primary" onClick={handleModalToggle}>
        Create Deployment
      </Button>
      <Modal
        variant={ModalVariant.medium}
        title="Create Deployment"
        description="Enter details below to create a new Deployment."
        isOpen={isModalOpen}
        onClose={handleModalToggle}
        actions={[
          <Button
            key="create"
            variant="primary"
            form="modal-with-form-create-deployment"
            type="submit"
            isDisabled={isSubmitting}
          >
            {isSubmitting ? (
              <div>
                <Spinner
                  isSVG
                  size="md"
                  aria-label="Contents of the small example"
                  style={{ '--pf-c-spinner--Color': 'white' }}
                />{' '}
                Creating deployment...
              </div>
            ) : (
              'Confirm'
            )}
          </Button>,
          <Button key="cancel" variant="link" onClick={handleModalToggle}>
            Cancel
          </Button>,
        ]}
      >
        <Flex justifyContent={{ default: 'justifyContentCenter' }} alignItems={{ default: 'alignItemsCenter' }}>
          <SelectionOption
            id="shared"
            title="Shared"
            subtitle={
              <div>
                <div>Single realm on our highly available clusters.</div>
                <div style={{ fontWeight: '600' }}>Always free.</div>
              </div>
            }
            listItems={['<1,000 users', '<10 SSO connections']}
          />
          <SelectionOption
            id="dedicated"
            title="Dedicated"
            subtitle={
              <div>
                <div>Dedicated single-tenant clusters starting at $499/month.</div>
                <div style={{ fontWeight: '600', color: 'var(--pf-global--palette--red-100)' }}>Coming soon!</div>
              </div>
            }
            listItems={[
              <div>
                Serverless{' '}
                <a href="https://www.cockroachlabs.com/product/" target="_black">
                  CockroachDB
                </a>{' '}
                instance
              </div>,
              'Unlimited users',
              'Unlimited SSO connections',
            ]}
          />
        </Flex>
        <Form id="modal-with-form-create-deployment" onSubmit={handleSubmit(submitForm)} style={{ marginTop: '2rem' }}>
          <FormGroup
            label="Name"
            labelIcon={
              <Popover
                headerContent={<div>Name</div>}
                bodyContent={<div>The name of the realm. This must be unique across all Phase Two Deployments.</div>}
              >
                <button
                  type="button"
                  aria-label="More info for name field"
                  onClick={(e) => e.preventDefault()}
                  aria-describedby="modal-with-form-form-name"
                  className="pf-c-form__group-label-help"
                >
                  <HelpIcon noVerticalAlign />
                </button>
              </Popover>
            }
            isRequired
            fieldId="modal-with-form-form-name"
            validated={isNameValid}
            helperText={nameHelperText}
            helperTextInvalid={nameHelperText}
            helperTextInvalidIcon={<ExclamationCircleIcon />}
          >
            <Controller
              name="name"
              control={control}
              rules={{ required: true, min: 3 }}
              render={({ field }) => (
                <TextInput
                  id="modal-with-form-form-name"
                  isRequired
                  type="text"
                  validated={isNameValid}
                  helperText={nameHelperText}
                  onKeyUp={checkAvailabilty}
                  {...field}
                />
              )}
            />
          </FormGroup>
          <FormGroup
            label="Organization"
            labelIcon={
              <Popover
                headerContent={<div>Organization</div>}
                bodyContent={
                  <div>
                    This will assign the owner Organization. You must be a manager in this organization. All other
                    members of the organization will be able to fully use and administer this Deployment.
                  </div>
                }
              >
                <button
                  type="button"
                  aria-label="More info for organization field"
                  onClick={(e) => e.preventDefault()}
                  aria-describedby="modal-with-form-form-email"
                  className="pf-c-form__group-label-help"
                >
                  <HelpIcon noVerticalAlign />
                </button>
              </Popover>
            }
            isRequired
            fieldId="modal-with-form-form-email"
          >
            <Controller
              name="organization"
              control={control}
              rules={{ required: true }}
              render={({ field }) => (
                <FormSelect id="organization-choose-form" aria-label="Choose an organizaton" {...field}>
                  <FormSelectOption value="" label="Select one" isDisabled isPlaceholder />
                  {manageRoleOrgs.map((option, index) => (
                    <FormSelectOption
                      key={option.id}
                      value={option.id}
                      label={`${option.displayName} (${option.id})`}
                    />
                  ))}
                </FormSelect>
              )}
            />
          </FormGroup>
          <FormGroup
            label="Region"
            labelIcon={
              <Popover headerContent={<div>Region</div>} bodyContent={<div>Region to deploy into.</div>}>
                <button
                  type="button"
                  aria-label="More info for region feld"
                  onClick={(e) => e.preventDefault()}
                  aria-describedby="modal-with-form-form-region"
                  className="pf-c-form__group-label-help"
                >
                  <HelpIcon noVerticalAlign />
                </button>
              </Popover>
            }
            isRequired
            fieldId="modal-with-form-form-region"
          >
            <Controller
              name="region"
              control={control}
              rules={{ required: true }}
              render={({ field }) => (
                <FormSelect id="region-choose-form" aria-label="Choose a region" {...field}>
                  <FormSelectOption value="" label="Select one" isDisabled isPlaceholder />
                  {regions.map((option) => (
                    <FormSelectOption
                      key={option.id}
                      value={option.id}
                      label={`${option.displayName} (${option.id})`}
                    />
                  ))}
                </FormSelect>
              )}
            />
          </FormGroup>
        </Form>
      </Modal>
    </>
  );
};
