import React, { useEffect, useState } from 'react';
import {
  Button,
  Modal,
  ModalVariant,
  Form,
  FormGroup,
  Popover,
  TextInput,
  Checkbox,
  Grid,
} from '@patternfly/react-core';
import { HelpIcon, ExclamationCircleIcon } from '@patternfly/react-icons';
import { useKeycloak } from '@react-keycloak/web';
import { OrganizationRepresentation } from 'src/query/orgs';
import { useForm, Controller } from 'react-hook-form';
import toast from 'react-hot-toast';
import { API } from 'src/api';
import { forIn, omit } from 'lodash';
import { defaultRoles } from '@app/utils/checkUserRoles';
const isValidDomain = require('is-valid-domain');

type Props = {
  refresh: () => void;
  clearOrg: () => void;
  org: OrganizationRepresentation;
};

const adminDRoles = defaultRoles.filter((r) => r.includes('manage') || r.includes('view'));
const developerDRoles = defaultRoles.filter((r) => r.includes('view'));

export const AddOrganizationDomains: React.FC<Props> = ({ refresh, org, clearOrg }) => {
  const { keycloak } = useKeycloak();
  const {
    handleSubmit,
    formState: { errors, isSubmitting },
    control,
    reset,
    setValue,
    watch,
  } = useForm({
    defaultValues: {
      domain: '',
    },
  });

  const submitForm = async (dep) => {
    if (org.domains?.includes(dep.domain)) {
      toast.error(
        `${dep.domain} is already part of organization ${org.displayName}.`
      );
    } else if (!isValidDomain(dep.domain)) {
      toast.error(
        `${dep.domain} is not a valid domain.`
      );
    } else {
      org.domains?.push(dep.domain);
      try {
        const updateOrgResp = await API.Orgs.editOrg({
          org: {
            ...org
          },
        });

        if (updateOrgResp.ok) {
          toast.success(`${dep.domain} has been added to organization ${org.displayName}`);
          refresh();
          clearOrg();
          return;
        }
        const errorResp = await updateOrgResp.json();
        toast.error(
          `${dep.domain} was not able to be added to organization ${org.displayName}. Please try again. (${errorResp.error})`
        );
      } catch (e) {
        console.log('[Error] Failed to update the organization.');
      }
    }
  };

  const hasError = (key: string) => (errors[key] ? 'error' : 'default');

  return (
    <>
      <Modal
        variant={ModalVariant.small}
        title="Add domain"
        description="Enter new domain to add to an organization."
        isOpen={true}
        onClose={clearOrg}
        actions={[
          <Button key="create" variant="primary" form="add-domain" type="submit">
            {isSubmitting ? 'Adding domain...' : 'Add'}
          </Button>,
          <Button key="cancel" variant="link" onClick={clearOrg}>
            Cancel
          </Button>,
        ]}
      >
        <Form id="add-domain" onSubmit={handleSubmit(submitForm)}>
          <FormGroup
            label="Domain"
            labelIcon={
              <Popover headerContent={<div>Domain</div>} bodyContent={<div>The FQDN of the domain to add (e.g. foo.com)</div>}>
                <button
                  type="button"
                  aria-label="More info for domain field."
                  onClick={(e) => e.preventDefault()}
                  aria-describedby="modal-with-form-form-domain"
                  className="pf-c-form__group-label-help"
                >
                  <HelpIcon noVerticalAlign />
                </button>
              </Popover>
            }
            isRequired
            fieldId="modal-with-form-form-domain"
            validated={hasError('domain')}
            helperTextInvalid={'Domain is a required field. '}
            helperTextInvalidIcon={<ExclamationCircleIcon />}
          >
            <Controller
              name="domain"
              control={control}
              rules={{ required: true, min: 3 }}
              render={({ field }) => (
                <TextInput
                  id="modal-with-form-form-domain"
                  isRequired
                  type="text"
                  validated={hasError('domain')}
                  {...field}
                />
              )}
            />
          </FormGroup>
        </Form>
      </Modal>
    </>
  );
};
