import React from 'react';
import { Button, Modal, ModalVariant, Form, FormGroup, Popover, TextInput } from '@patternfly/react-core';
import { HelpIcon, ExclamationCircleIcon } from '@patternfly/react-icons';
import { OrganizationRepresentation } from 'src/query/orgs';
import { useForm, Controller } from 'react-hook-form';
import toast from 'react-hot-toast';
import { API } from 'src/api';

type Props = {
  refresh: () => void;
  clearOrg: () => void;
  org: OrganizationRepresentation;
};

export const EditOrganization: React.FC<Props> = ({ refresh, org, clearOrg }) => {
  const {
    handleSubmit,
    formState: { errors, isSubmitting },
    control,
  } = useForm({ defaultValues: org });

  const submitForm = async (dep) => {
    try {
      const updateOrgResp = await API.Orgs.editOrg({
        org: {
          ...org,
          displayName: dep.displayName,
        },
      });

      if (updateOrgResp.ok) {
        toast.success(`${org.displayName} has been updated to ${dep.displayName}`);
        refresh();
        clearOrg();
        return;
      }
      const errorResp = await updateOrgResp.json();
      toast.error(
        `${org.displayName} was not able to be updated to ${dep.displayName}. Please try again. (${errorResp.error})`
      );
    } catch (e) {
      console.log('[Error] Failed to update the organization.');
    }
  };

  const hasError = (key: string) => (errors[key] ? 'error' : 'default');

  return (
    <Modal
      variant={ModalVariant.small}
      title="Edit Organization"
      description="Edit Organization details below."
      isOpen={true}
      onClose={clearOrg}
      actions={[
        <Button key="create" variant="primary" form="modal-with-form-organization-form" type="submit">
          {isSubmitting ? 'Saving...' : 'Save'}
        </Button>,
        <Button key="cancel" variant="link" onClick={clearOrg}>
          Cancel
        </Button>,
      ]}
    >
      <Form id="modal-with-form-organization-form" onSubmit={handleSubmit(submitForm)}>
        <FormGroup label="Id" fieldId="modal-with-form-form-id">
          <TextInput id="modal-with-form-form-id" isDisabled type="text" isReadOnly value={org.id} />
        </FormGroup>
        <FormGroup label="Name" fieldId="modal-with-form-form-name">
          <TextInput id="modal-with-form-form-name" isDisabled type="text" isReadOnly value={org.name} />
        </FormGroup>
        <FormGroup
          label="Display Name"
          labelIcon={
            <Popover
              headerContent={<div>Display Name</div>}
              bodyContent={<div>The display name of the organization.</div>}
            >
              <button
                type="button"
                aria-label="More info for name field"
                onClick={(e) => e.preventDefault()}
                aria-describedby="modal-with-form-form-name"
                className="pf-c-form__group-label-help"
              >
                <HelpIcon noVerticalAlign />
              </button>
            </Popover>
          }
          isRequired
          fieldId="modal-with-form-form-name"
          validated={hasError('displayName')}
          helperTextInvalid={'Display name is a required field. Must be at least 3 characters.'}
          helperTextInvalidIcon={<ExclamationCircleIcon />}
        >
          <Controller
            name="displayName"
            control={control}
            rules={{ required: true, min: 3 }}
            render={({ field }) => (
              <TextInput
                id="modal-with-form-form-name"
                isRequired
                type="text"
                validated={hasError('name')}
                {...field}
              />
            )}
          />
        </FormGroup>
      </Form>
    </Modal>
  );
};
