/* eslint-disable no-var */
// @ts-nocheck

import { useEffect } from 'react';
import { useKeycloak } from '@react-keycloak/web';

function ChatwootWidget() {
  const { keycloak } = useKeycloak();

  useEffect(() => {
    window.addEventListener("chatwoot:ready", function () {
      setUserDetails();
    });	

    // Add Chatwoot Settings
    window.chatwootSettings = {
      hideMessageBubble: false,
      position: 'right', // This can be left or right
      locale: 'en', // Language to be set
      type: 'standard', // [standard, expanded_bubble]
    };

    // Paste the script from inbox settings except the <script> tag
    (function (d, t) {
      var BASE_URL = 'https://app.chatwoot.com';
      var g = d.createElement(t),
        s = d.getElementsByTagName(t)[0];
      g.src = BASE_URL + '/packs/js/sdk.js';
      g.defer = true;
      g.async = true;
      s.parentNode.insertBefore(g, s);
      g.onload = function () {
        window.chatwootSDK.run({
          websiteToken: 'S8t3eGYFdUrstXcfo257v1Ph',
          baseUrl: BASE_URL,
        });
      };
    })(document, 'script');
  }, []);

  const setUserDetails = async () => {
    if (!keycloak.tokenParsed?.email) {
      return setTimeout(setUserDetails, 1000);
    }

    if (keycloak.tokenParsed?.email && window.$chatwoot) {
      window.$chatwoot.setUser(keycloak.tokenParsed?.sub, {
        name: keycloak.tokenParsed?.preferred_username,
        email: keycloak.tokenParsed?.email,
      });
    }
  };

  return null;
}

export default ChatwootWidget;
