import { generateBasePath } from '@app/routes';
import {
  Breadcrumb,
  BreadcrumbItem,
  Button,
  EmptyState,
  EmptyStateIcon,
  EmptyStateVariant,
  PageSection,
  Title,
} from '@patternfly/react-core';
import { UsersIcon } from '@patternfly/react-icons';
import React, { useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { useGetOrganizationById } from 'src/query/orgs';
import { InviteOrganizationMembers, OrganizationMembersTable } from './components';

const OrganizationMembers: React.FunctionComponent = () => {
  const { organization_id: orgId } = useParams();
  const [inviteMember, setInviteMember] = useState(false);
  const { data: activeOrg, refetch } = useGetOrganizationById({
    pathParams: { realm: 'self', orgId },
  });

  return (
    <PageSection>
      <Breadcrumb>
        <BreadcrumbItem component="div">
          <Link to={generateBasePath('/organizations')}>Organizations</Link>
        </BreadcrumbItem>
        <BreadcrumbItem to="#" isActive>
          {activeOrg ? activeOrg.displayName : ''}
        </BreadcrumbItem>
        <BreadcrumbItem to="#" isActive>
          Members
        </BreadcrumbItem>
      </Breadcrumb>
      <EmptyState variant={EmptyStateVariant.full}>
        <EmptyStateIcon icon={UsersIcon} />
        <Title headingLevel="h1" size="lg">
          {activeOrg ? activeOrg.displayName : 'Organization'} Members
        </Title>
        <Button variant="primary" onClick={() => setInviteMember(true)}>
          Invite new member
        </Button>
        {inviteMember && (
          <InviteOrganizationMembers refresh={refetch} orgId={orgId} closeModal={() => setInviteMember(false)} />
        )}
      </EmptyState>
      <OrganizationMembersTable />
    </PageSection>
  );
};

export { OrganizationMembers };
