import React, { useEffect, useState } from 'react';
import { TableComposable, Thead, Tr, Th, Tbody, Td } from '@patternfly/react-table';
import { Deployment } from '../Deployments';
import {
  Button,
  OverflowMenu,
  OverflowMenuContent,
  OverflowMenuGroup,
  OverflowMenuItem,
  Title,
  EmptyState,
  EmptyStateVariant,
  EmptyStateIcon,
  EmptyStateBody,
  Bullseye,
  Spinner,
} from '@patternfly/react-core';
import {
  ExclamationCircleIcon,
  ExternalLinkSquareAltIcon,
  SearchIcon,
  CheckCircleIcon,
  BanIcon,
  InProgressIcon,
} from '@patternfly/react-icons';
import { fetchCreateDeploymentConsoleLink, GetDeploymentsError, GetDeploymentsResponse } from 'src/query/deployments';
import { useKeycloak } from '@react-keycloak/web';
import { ConfirmationDialog } from '@app/components/ConfirmationDialog/confirmation-dialog';
import toast from 'react-hot-toast';
import { DateTime } from 'luxon';
import { API } from 'src/api';

const columnNames = {
  name: 'Name',
  id: 'ID',
  createdAt: 'Created',
  state: 'State',
  action: 'Actions',
};

const stateIcon = (state: Deployment['state']) => {
  if (state === 'ACTIVE') {
    return (
      <div style={{ display: 'flex', alignItems: 'center' }}>
        <CheckCircleIcon color="var(--pf-global--success-color--100)" style={{ paddingRight: '.3rem' }} /> active
      </div>
    );
  }
  if (state === 'PENDING') {
    return (
      <div style={{ display: 'flex', alignItems: 'center' }}>
        <InProgressIcon color="var(--pf-global--warning-color--100)" style={{ paddingRight: '.3rem' }} /> pending
      </div>
    );
  }
  if (state === 'DISABLED') {
    return (
      <div style={{ display: 'flex', alignItems: 'center' }}>
        <BanIcon color="var(--pf-global--danger-color--100)" style={{ paddingRight: '.3rem' }} /> disabled
      </div>
    );
  }
};

type Props = {
  refresh: string;
  deploys: GetDeploymentsResponse;
  isLoading: boolean;
  refetch: () => void;
  error: GetDeploymentsError | null;
};

export const DeploymentsTable: React.FC<Props> = ({ refresh, deploys, error, isLoading, refetch }) => {
  const { keycloak } = useKeycloak();
  const [deleteDeployment, setDeleteDeployment] = useState<Deployment | null>(null);
  const [isDeletingDeployment, setisDeletingDeployment] = useState(false);

  const deleteADeployment = async () => {
    setisDeletingDeployment(true);
    toast('Deleting deployment. Please standby as this can take a few moments...');
    try {
      const deletedDeployment = await API.Orgs.deleteDeployment({ deploymentId: deleteDeployment?.id });

      if (deletedDeployment.ok) {
        toast.success('Deployment deleted.');
        setDeleteDeployment(null);
        refetch();
        return;
      } else {
        console.log('[Error] Could not delete deployment.');
      }
    } catch (e) {
      console.log('[Error] Was not able to delete deployment.');
    }
    setisDeletingDeployment(false);
    toast.error('Error trying to delete deployment. Please try again.');
  };

  useEffect(() => {
    refetch();
  }, [refresh]);

  const getConsoleLink = (deploymentId) => {
    const linkPromise = fetchCreateDeploymentConsoleLink({
      pathParams: {
        realm: 'self',
        deploymentId,
      },
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
    });
    toast.promise(linkPromise, {
      loading: 'Generating console link. Please standby.',
      success: () => {
        linkPromise.then((l) => window.open(l.link));
        return 'Link generated successfully.';
      },
      error: 'Error generating link. Please try again.',
    });
  };

  return (
    <>
      {deleteDeployment && (
        <ConfirmationDialog
          isOpen={!!deleteDeployment}
          title={`Delete this deployment: ${deleteDeployment.name}`}
          body="Deleting this deployment will remove all data associated with this realm. This action cannot be undone."
          confirmText={
            isDeletingDeployment ? (
              <div>
                <Spinner
                  isSVG
                  size="md"
                  aria-label="Contents of the small example"
                  style={{ '--pf-c-spinner--Color': 'white' }}
                />{' '}
                Deleting deployment...
              </div>
            ) : (
              'Delete'
            )
          }
          confirmVariant="danger"
          onModalCancel={() => setDeleteDeployment(null)}
          onModalConfirm={() => deleteADeployment()}
          disableConfirm={isDeletingDeployment}
        />
      )}
      <TableComposable aria-label="Actions table">
        <Thead>
          <Tr>
            <Th>{columnNames.id}</Th>
            <Th>{columnNames.name}</Th>
            <Th>{columnNames.state}</Th>
            <Th>{columnNames.createdAt}</Th>
            <Th></Th>
          </Tr>
        </Thead>
        <Tbody>
          {isLoading && (
            <Tr>
              <Td colSpan={5}>
                <Bullseye>
                  <EmptyState>
                    <EmptyStateIcon variant="container" component={Spinner} />
                    <Title size="lg" headingLevel="h2">
                      Loading
                    </Title>
                  </EmptyState>
                </Bullseye>
              </Td>
            </Tr>
          )}
          {error && !isLoading && (
            <Tr>
              <Td colSpan={5}>
                <EmptyState variant={EmptyStateVariant.small}>
                  <EmptyStateIcon icon={ExclamationCircleIcon} />
                  <Title headingLevel="h2" size="lg">
                    No Deployments found
                  </Title>
                  <EmptyStateBody>
                    There was an error retrieving data. Check your connection and reload the page.
                  </EmptyStateBody>
                </EmptyState>
              </Td>
            </Tr>
          )}
          {deploys.length === 0 && !isLoading && !error && (
            <Tr>
              <Td colSpan={5}>
                <EmptyState>
                  <EmptyStateIcon icon={SearchIcon} />
                  <Title headingLevel="h2" size="lg">
                    No Deployments found
                  </Title>
                  <EmptyStateBody>No Deployments are currently created. Create a Deployment above.</EmptyStateBody>
                </EmptyState>
              </Td>
            </Tr>
          )}
          {deploys.length > 0 &&
            !isLoading &&
            !error &&
            deploys.map((deploy) => {
              const isCreator = deploy.created_by_user_id === keycloak.tokenParsed?.sub;
              const dt = DateTime.fromMillis(deploy.created_at);

              return (
                <Tr key={deploy.name}>
                  <Td dataLabel={columnNames.id}>{deploy.id}</Td>
                  <Td dataLabel={columnNames.name}>{deploy.name}</Td>
                  <Td dataLabel={columnNames.name}>{stateIcon(deploy.state)}</Td>
                  <Td dataLabel={columnNames.createdAt}>{`${dt.toLocaleString(DateTime.DATE_SHORT)} ${dt.toLocaleString(
                    DateTime.TIME_24_WITH_SECONDS
                  )}`}</Td>
                  <Td isActionCell>
                    <OverflowMenu breakpoint="md">
                      <OverflowMenuContent>
                        <OverflowMenuGroup groupType="button">
                          <OverflowMenuItem>
                            <Button
                              variant="link"
                              icon={<ExternalLinkSquareAltIcon />}
                              onClick={() => getConsoleLink(deploy.id)}
                            >
                              Open Console
                            </Button>
                          </OverflowMenuItem>
                          <OverflowMenuItem>
                            <Button
                              onClick={() => setDeleteDeployment(deploy)}
                              variant="link"
                              isDanger
                              isDisabled={!isCreator}
                              title={
                                isCreator
                                  ? 'This will delete the realm and all data associated.'
                                  : 'Only the creator of a deployment can delete that deployment.'
                              }
                            >
                              Delete
                            </Button>
                          </OverflowMenuItem>
                        </OverflowMenuGroup>
                      </OverflowMenuContent>
                    </OverflowMenu>
                  </Td>
                </Tr>
              );
            })}
        </Tbody>
      </TableComposable>
    </>
  );
};
