import React, { useEffect, useState } from 'react';
import { CloudUploadAltIcon } from '@patternfly/react-icons';
import {
  PageSection,
  Title,
  EmptyState,
  EmptyStateVariant,
  EmptyStateIcon,
  EmptyStateBody,
  Text,
  TextContent,
  TextVariants,
} from '@patternfly/react-core';
import { CreateDeployment, DeploymentsTable } from './components';
import { useGetDeployments } from 'src/query/deployments';
import { useKeycloak } from '@react-keycloak/web';

export interface Deployment {
  id: string;
  name: string;
  state: 'ACTIVE' | 'PENDING' | 'DISABLED';
  created_at: string;
  created_by_user_id: string;
}

export interface IDeploymentsProps {
  sampleProp?: string;
}

export const deploymentModalKey = 'p2_selfserve_create_deployment_modal';

// eslint-disable-next-line prefer-const
let Deployments: React.FC<IDeploymentsProps> = () => {
  const { keycloak } = useKeycloak();
  const [refresh, setRefresh] = useState('');
  const [isModalOpen, setModalOpen] = useState(false);
  const [showCTA, setShowCTA] = useState(false);

  useEffect(() => {
    if (localStorage.getItem(deploymentModalKey) === null) {
      localStorage.setItem(deploymentModalKey, 'false');
    }
  }, []);

  const {
    data: deploys = [],
    error,
    isLoading,
    isFetched,
    refetch,
  } = useGetDeployments({
    pathParams: { realm: 'self' },
  });

  useEffect(() => {
    if (isFetched && localStorage.getItem(deploymentModalKey) === 'false' && deploys.length === 0) {
      // open modal
      setModalOpen(true);
      localStorage.setItem(deploymentModalKey, 'true');
    }
  }, [isFetched]);

  const checkDeploysCreatedByUser = async () => {
    const hasDeploys = deploys.map((deploy) => deploy.created_by_user_id === keycloak.tokenParsed?.sub);
    if (hasDeploys.includes(true)) {
      setShowCTA(false);
    } else {
      setShowCTA(true);
    }
  };

  useEffect(() => {
    checkDeploysCreatedByUser();
  }, [deploys]);

  return (
    <PageSection>
      <EmptyState variant={EmptyStateVariant.full}>
        <EmptyStateIcon icon={CloudUploadAltIcon} />
        <Title headingLevel="h1" size="lg">
          Deployments
        </Title>
        {showCTA && (
          <EmptyStateBody>
            <TextContent>
              <Text component="p">
                Each account can create a deployment using our free plan for testing and development.
              </Text>
              <Text component={TextVariants.small}>Get started with your free deployment today!</Text>
            </TextContent>
          </EmptyStateBody>
        )}
        <CreateDeployment refresh={setRefresh} isModalOpen={isModalOpen} setModalOpen={setModalOpen} />
      </EmptyState>
      <DeploymentsTable refresh={refresh} deploys={deploys} error={error} isLoading={isLoading} refetch={refetch} />
    </PageSection>
  );
};

export { Deployments };
